/** Making Osler Alumni base styles available in the CKeditor These must be manually copied from the build folder in to the CMS.  Paste the styles in to the Editor CSS Stylesheet box under Sites. */
@font-face { font-family: "CelesteSans-Regular"; src: url("fonts/celeste-sans/CelesteSansWeb.eot"); src: url("fonts/celeste-sans/CelesteSansWeb.eot?#iefix") format("embedded-opentype"), url("fonts/celeste-sans/CelesteSansWeb.woff") format("woff"), url("fonts/celeste-sans/CelesteSansComp.ttf") format("truetype"); font-weight: normal; font-style: normal; }

@font-face { font-family: "CelesteSans-Bold"; src: url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.eot"); src: url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.eot?#iefix") format("embedded-opentype"), url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.woff2") format("woff2"), url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.woff") format("woff"), url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.ttf") format("truetype"), url("fonts/celeste-sans/bold/celestesanscomp-bold-webfont.svg#celestesanscomp-boldregular") format("svg"); font-weight: normal; font-style: normal; }

@font-face { font-family: "CelesteSans-Italic"; src: url("fonts/celeste-sans/italic/CelesteSansRegularItalic.eot"); src: url("fonts/celeste-sans/italic/CelesteSansRegularItalic.eot?#iefix") format("embedded-opentype"), url("fonts/celeste-sans/italic/CelesteSansRegularItalic.woff") format("woff"), url("fonts/celeste-sans/italic/CelesteSansItalic.ttf") format("truetype"); font-weight: normal; font-style: normal; }

/*
=============================================================================
                   Normalize, border box and clearfix
=============================================================================
*/
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary { display: block; }

audio, canvas, video { display: inline-block; }

audio:not([controls]) { display: none; height: 0; }

[hidden] { display: none; }

html { font-family: sans-serif; -webkit-text-size-adjust: 100%; -ms-text-size-adjust: 100%; }

a:active, a:hover { outline: 0; }

h1 { font-size: 2em; }

abbr[title] { border-bottom: 1px dotted; }

b, strong { font-weight: 700; }

dfn { font-style: italic; }

mark { background: #ff0; color: #000; }

code, kbd, pre, samp { font-family: monospace, serif; font-size: 1em; }

pre { white-space: pre-wrap; word-wrap: break-word; }

q { quotes: \201C \201D \2018 \2019; }

small { font-size: 80%; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sup { top: -.25em; }

sub { bottom: -.25em; }

img { border: 0; }

svg:not(:root) { overflow: hidden; }

fieldset { border: 1px solid silver; margin: 0 2px; padding: .35em .625em .75em; }

button, input, select, textarea { font-family: inherit; font-size: 100%; margin: 0; }

button, input { line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] { -webkit-appearance: button; cursor: pointer; }

button[disabled], input[disabled] { cursor: default; }

input[type=checkbox], input[type=radio] { box-sizing: border-box; padding: 0; }

input[type=search] { -webkit-appearance: textfield; -moz-box-sizing: content-box; -webkit-box-sizing: content-box; box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration { -webkit-appearance: none; }

textarea { overflow: auto; vertical-align: top; }

table { border-collapse: collapse; border-spacing: 0; }

body, figure { margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner { border: 0; padding: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.clearfix:after { content: " "; visibility: hidden; display: block; height: 0; clear: both; }

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; }

html { display: flex; font-size: 1rem; }

body { font-family: "CelesteSans-Regular", Helvetica, Arial, sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; display: flex; color: #414141; flex-flow: column nowrap; line-height: 26px; min-height: 100vh; width: 100%; overflow-x: hidden; }

:focus { outline: 3px dotted #355E7C !important; }

header, footer { flex-shrink: 0; }

main { display: flex; flex-direction: column; flex-wrap: nowrap; flex-grow: 1; font-size: 1rem; position: relative; }

main > div { padding: 40px 0; }

@media print, screen and (min-width: 40em) { main { font-size: 1.125rem; } }

.row { width: 100%; }

.c-home { padding: 0; }

.b-lazy { opacity: 0; transform: scale(0); transition: all 500ms; }

.b-loaded { opacity: 1; transform: scale(1); }

input[type="text"]::-ms-clear { display: none; }

.is-red { color: #CD153E !important; }

.is-grey { color: #414141 !important; }

div:focus { outline: none !important; }

img { max-width: 100%; }

button { cursor: pointer; transition: all 150ms ease; }

h1, h2, h3, h4, h5, h6 { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; margin-bottom: 1rem; }

h1 { color: #414141; font-size: 32px; line-height: 1.2; }

@media print, screen and (min-width: 40em) { h1 { font-size: 50px; } }

h2 { color: #414141; font-size: 30px; line-height: 1.2; }

@media print, screen and (min-width: 40em) { h2 { font-size: 40px; } }

h3 { color: #414141; font-size: 28px; line-height: 1.2; }

@media print, screen and (min-width: 40em) { h3 { font-size: 30px; } }

h4 { color: #414141; font-size: 24px; line-height: 1.2; }

@media print, screen and (min-width: 40em) { h4 { font-size: 28px; } }

h5 { color: #414141; font-size: 22px; line-height: 1.2; }

@media print, screen and (min-width: 40em) { h5 { font-size: 24px; } }

h6 { color: #CD153E; font-size: 18px; line-height: 1.2; text-transform: uppercase; }

@media print, screen and (min-width: 40em) { h6 { font-size: 20px; } }

a { color: currentColor; transition: all 150ms ease; text-decoration: underline; line-height: 1.3; }

a:hover, a:focus { text-decoration: none; }

a.is-small { color: #355E7C; font-size: 1rem; line-height: 24px; text-decoration: underline; }

a.is-large { font-size: 1.25rem; }

@media print, screen and (min-width: 40em) { a.is-large { font-size: 1.5rem; } }

a.is-mini { font-size: 0.875rem; line-height: 26px; }

a.is-bold { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; }

a.is-inline:not(.c-link):not(.c-button) { text-decoration: underline; color: #355E7C; }

a.is-inline:not(.c-link):not(.c-button):hover, a.is-inline:not(.c-link):not(.c-button):focus { text-decoration: none; }

a.no-underline { text-decoration: none; }

a.no-underline:hover, a.no-underline:focus { text-decoration: underline; }

a.is-external:after { background: url("/build/images/icons/ico-external.svg") no-repeat center; }

a.is-internal:after { background: url("/build/images/icons/ico-page.svg") no-repeat center; }

a.is-external.is-file:after, a.is-internal.is-file:after { background: url("/build/images/icons/ico-file.svg") no-repeat center; }

a.is-internal, a.is-external, a.is-file { display: inline-block; }

a.is-internal:after, a.is-external:after, a.is-file:after { content: ''; position: absolute; background-size: 1.15rem; width: 1.4rem; height: 1.4rem; margin-left: 5px; bottom: 2px; }

@media print, screen and (min-width: 40em) { a.is-internal:after, a.is-external:after, a.is-file:after { background-size: 1.15rem; width: 1.4rem; height: 1.4rem; bottom: auto; } }

a.is-internal, a.is-external, a.is-file { position: relative; }

h2 a { display: inline-block; }

@media print, screen and (min-width: 40em) { h2 a.is-internal:after, h2 a.is-external:after { background-size: 20px; width: 1.75rem; height: 2rem; } }

.c-feature-content h2 a.is-internal:after, .c-feature-content h2 a.is-external:after { bottom: 5px; }

p { margin-bottom: 0.75em; font-size: 1.125rem; line-height: 1.15; }

@media print, screen and (min-width: 40em) { p { font-size: 1.25rem; } }

p.is-description { font-size: 1.125rem; color: #414141; }

@media print, screen and (min-width: 40em) { p.is-description { font-size: 1.25rem; } }

p.is-small { color: #414141; font-size: 1rem; }

@media print, screen and (min-width: 40em) { p.is-small { font-size: 1.125rem; } }

p.is-medium { font-size: 1.125rem; }

p.is-large { font-size: 1.125rem; font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; }

@media print, screen and (min-width: 40em) { p.is-large { font-size: 1.25rem; } }

p.is-large span { font-family: "CelesteSans-Regular", Helvetica, Arial, sans-serif; }

p.is-large-body { color: inherit; font-size: 1.125rem; font-family: "CelesteSans-Regular", Helvetica, Arial, sans-serif; }

@media print, screen and (min-width: 40em) { p.is-large-body { font-size: 1.25rem; } }

p.is-regular { font-family: "CelesteSans-Regular", Helvetica, Arial, sans-serif; }

p.is-bold { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; }

p.is-navy { color: #355E7C; }

p.is-caps { text-transform: uppercase; }

p.is-title { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; font-size: 1.25rem; }

@media print, screen and (min-width: 40em) { p.is-title { font-size: 1.5rem; } }

p.is-tag { font-size: 1rem; }

@media print, screen and (min-width: 40em) { p.is-tag { font-size: 1.125rem; } }

em { font-family: "CelesteSans-Italic", Helvetica, Arial, sans-serif; }

strong em { font-weight: bold; }

hr { border: none; height: 2px; background-color: #CD153E; margin: 0; }

strong { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; }

em strong { font-style: italic; }

ol { padding-left: 20px; }

ul { margin-left: 15px; padding-bottom: 15px; padding-left: 20px; list-style: disc; }

ul li { padding-left: 1.625rem; }

ol, ul { font-size: 1.125rem; }

@media print, screen and (min-width: 40em) { ol, ul { font-size: 1.25rem; } }

ol li ul, ol li ol, ul li ul, ul li ol { margin-top: 0.25em; }

ol li + li, ul li + li { margin-top: 0.25em; }

li a { vertical-align: top; }

.unstyled { list-style: none; margin: 0; padding: 0; }

.unstyled li { margin: 0; }

.unstyled li { padding-left: 0; }

.has-unstyled-list ul { list-style: none; margin: 0; padding: 0; }

.has-unstyled-list ul li { margin: 0; }

.has-unstyled-list ul li { padding-left: 0; }

.has-unstyled-list ul li:before { content: none !important; }

blockquote { margin: 0; padding: 0; border: none; line-height: inherit; color: inherit; }

/* Figure - Blockquote - Figcaption for blockquote with attribution */
figure.e-blockquote-wrap { margin: 40px 20px; padding-left: 20px; }

@media print, screen and (min-width: 40em) { figure.e-blockquote-wrap { margin: 40px 60px; } }

figure.e-blockquote-wrap blockquote { color: #CD153E; font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; font-size: 1.375rem; line-height: 30px; position: relative; }

figure.e-blockquote-wrap blockquote:before { display: block; content: "\201C"; font-size: 1.875rem; position: absolute; left: -16px; line-height: 30px; top: 3px; color: #CD153E; }

@media print, screen and (min-width: 40em) { figure.e-blockquote-wrap blockquote { font-size: 1.875rem; line-height: 38px; } }

figure.e-blockquote-wrap figcaption { margin-top: 15px; }

figure.e-blockquote-wrap figcaption, figure.e-blockquote-wrap figcaption p { color: #CD153E; font-size: 1rem; line-height: 1.25rem; margin-bottom: 0; }

span.is-mini { font-size: 0.875rem; line-height: 26px; }

span.is-bold { font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; }

.c-button { border: 0; display: inline-flex; align-items: center; justify-content: center; cursor: pointer; font-family: "CelesteSans-Bold", Helvetica, Arial, sans-serif; transition: all 150ms ease; text-decoration: none; max-width: 100%; }

.c-button:hover, .c-button:focus { text-decoration: none; }

.c-button-primary { background-color: #355E7C; border-radius: 3px; color: #C8DBE1; font-size: 1rem; line-height: 18px; padding: 15px 20px; text-transform: uppercase; }

.c-button-primary:hover, .c-button-primary:focus { background-color: #C8DBE1; color: #355E7C; }

.c-button-primary:disabled:hover { background-color: #E3EDF0; color: #355E7C; }

.c-button-secondary { background-color: #C8DBE1; border-radius: 3px; color: #355E7C; font-size: 1rem; line-height: 18px; padding: 15px 20px; text-transform: uppercase; }

.c-button-secondary:hover, .c-button-secondary:focus { background-color: #355E7C; color: #C8DBE1; }

.c-button-secondary:disabled:hover { background-color: #E3EDF0; color: #355E7C; }

.c-button-alternate { background-color: #ffffff; border-radius: 3px; color: #414141; font-size: 1rem; line-height: 18px; padding: 15px 20px; text-transform: uppercase; }

.c-button-alternate:hover, .c-button-alternate:focus { background-color: #C8DBE1; color: #355E7C; }

.c-button-alternate:disabled:hover { background-color: #E3EDF0; color: #355E7C; }

.c-button-login { background-color: #355E7C; border-radius: 3px; color: #C8DBE1; font-size: 1rem; line-height: 18px; padding: 15px 20px; }

.c-button-login:hover, .c-button-login:focus { background-color: #C8DBE1; color: #355E7C; }

@media screen and (max-width: 63.99875em) { .c-button-login { background-color: transparent; border: 1px solid #ffffff; color: #ffffff; }
  .c-button-login:hover, .c-button-login:focus { background-color: #ffffff; color: #414141; } }

.c-button-logout { background-color: transparent; border-radius: 3px; border: 1px solid #ffffff; color: #ffffff; font-size: 1rem; line-height: 18px; padding: 15px 20px; }

